// Masthead
//
// Super small header above the content for site name and short description.

.masthead {
  padding-top: var(--spacer);
  padding-bottom: var(--spacer);
  margin-bottom: var(--spacer-3);
}

.masthead-title {
  margin-bottom: 0;

  a {
    color: inherit;
    text-decoration: none;
  }

  small {
    font-weight: 400;
    opacity: 0.5;
  }
}

// Navbar styles
.nav {
  float: right;
  line-height: 1.25rem;
  word-spacing: 1rem;
}

@media screen and (max-width: 500px) {
  .masthead-title {
    text-align: center;

    a {
      display: inline-block;
    }

    small {
      font-size: medium;
    }
  }

  .nav {
    display: inline-block;
    float: none;
  }
}
